@import "./global.scss";

.App {
  width: 100%;
  min-height: 700px;
  height: 100vh;
  position: absolute;
  top: 0;
  overflow: hidden;
  //display: flex;
  //flex-direction: column;
  //align-items: center;
  //justify-content: center;
  text-align: center;
  background-color: orange;

  .noData {
    width: 100%;
    height: 100%;
    background-color: $backgroundColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    h1 {
      color: $textColor;
    }
  }


  .sections {
    position: absolute;
    width: 100%;
    height: 100%;
    //z-index: 2;
    background-color: white;
    //position: relative;
    scroll-behavior: auto;
    scroll-snap-type: y mandatory;
    scroll-snap-stop: always;
    scrollbar-width: none;
    //scroll-padding-top: $searchBarHeight; // ?
    //scroll-margin-top: $searchBarHeight;


    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: calc(100% - 20px);
      height: 100%;
      margin-left: 10px;
      position: relative;
      padding-top: 70px;
      background-color: darkgreen;
      scroll-snap-align: start;
    }
  }
}
